function NumberToImg(prop) {
  let number = prop.number;
  if (number === 0) {
    return <img className="img-fluid piece" src={blank} alt="0" />
  } else if (number === 1) {
    return <img className="img-fluid piece" src={fuhyou} alt="1" />
  } else {
    return <img className="img-fluid piece" src={tokin} alt="2" />
  }
}

function Sqeuare(props) {
  return (
    <div className="btn btn-light content" onClick={props.onClick} id={props.xKey + "-" + props.yKey}>
      <NumberToImg number={props.value} />
    </div>
  )
}

