class Game extends React.Component {
  constructor(props) {
    let board = new BoardComponent();
    let boardInfo = board.boardInfo;
    super(props);
    this.state = {
      turn: 1,
      squares: boardInfo,
      fromSquare: [],
      toSquare: [],
      BLANK: 0,
      PLAYER1: 1,
      PLAYER2: 2,
    };
  }

  onClickPiece(x, y) {
    let fromSquare = this.state.fromSquare;
    // let toSquare = this.state.toSquare;
    let squares = this.state.squares;
    let legalMoveList;
    let illegalFlag = true;

    // console.log(this.state.turn);
    // console.log("length:" + this.state.fromSquare.length);


    // 移動元の選択
    if (fromSquare.length === 0) {
      console.log("selected from!");
      if (squares[y][x] === this.state.turn) {
        console.log("squares:" + squares[y][x]);
        this.setState({ fromSquare: [x, y] });
      } else if (squares[y][x] === this.oppose(this.state.turn)) {
        console.log("not your turn!");
      } else {
        console.log("please select piece!");
      }
      // 移動先の選択.
    } else {
      // console.log("TO!");
      // 移動可能か調べる.
      legalMoveList = this.searchLigalMove(fromSquare);
      for (let i = 0; i < legalMoveList.length; i++) {

        if (legalMoveList[i][0] === x && legalMoveList[i][1] === y) {

          squares = this.movePiece(squares, this.state.fromSquare, [x, y]);
          illegalFlag = false;
          this.setState({ turn: this.oppose(this.state.turn) });

          break;

        }

      }

      if (illegalFlag === true) {
        console.log("please select correct pieces!");
      }

    }


    if (this.state.fromSquare.length > 0) {
      this.setState({
        fromSquare: [],
        squares: squares
      })
    }

  }

  movePiece(squares, fromSquare, toSquare) {
    let fromX = fromSquare[0];
    let fromY = fromSquare[1];
    let toX = toSquare[0];
    let toY = toSquare[1];
    let count = 0;
    let piece = squares[fromY][fromX];
    // console.log(this.state);
    // console.log("piece: " + piece);
    // console.log(squares);
    for (let iX = -1; iX <= 1; iX++) {

      for (let iY = -1; iY <= 1; iY++) {

        if ((iX + iY === 0) || (Math.abs(iX) + Math.abs(iY) === 2)) {

          continue;

        } else {

          count = this.countCatchPiece(squares, toSquare, iX, iY, piece);
          // console.log("count: " + count);

          // 挟んでいるコマのいるマスを空白にする.
          for (let c = 1; c <= count; c++) {

            squares[toY + iY * c][toX + iX * c] = 0;

          }

        }

      }

    }

    squares[fromY][fromX] = 0;
    squares[toY][toX] = piece;
    squares = this.catchCornerPiece(squares);
    // console.log("changed!")
    // console.log(squares);
    return squares;
  }


  /**
   * 
   * @param {コマを置く予定のマス} setPosition 
   * @param {調べる方向} direction 
   * @param {} player 
   */
  countCatchPiece(squares, setPosition, dirX, dirY, player) {
    let posX = setPosition[0];
    let posY = setPosition[1];
    let i;

    for (i = 1; squares[posY + i * dirY][posX + i * dirX] === this.oppose(player); i++);


    if (squares[posY + i * dirY][posX + i * dirX] === player) {
      return i - 1;
    } else {
      return 0;
    }

  }

  oppose(player) {
    return player === 1 ? this.state.PLAYER2 : this.state.PLAYER1;
  }

  searchLigalMove(position) {

    let posX = position[0];
    let posY = position[1];
    let toX;
    let toY;
    let count = 0;
    let legalMoveList = [];

    for (let iX = -1; iX <= 1; iX++) {

      for (let iY = -1; iY <= 1; iY++) {

        if ((iX + iY === 0) || (Math.abs(iX) + Math.abs(iY) === 2)) {

          continue;

        } else {

          count = this.searchBlankPosition(position, iX, iY);
          // console.log("count: " + count);

          for (let c = 1; c <= count; c++) {

            toX = posX + iX * c;
            toY = posY + iY * c;

            // console.log("posX: " + toX);
            // console.log("posY: " + toY);
            legalMoveList.push([toX, toY])

          }

        }

      }

    }

    return legalMoveList;

  }

  // 一方向について置くことが可能な
  //現在地から最も遠い座標を返す
  searchBlankPosition(position, dirX, dirY) {

    let squares = this.state.squares;
    let posX = position[0];
    let posY = position[1];
    let i;

    for (i = 1; squares[posY + i * dirY][posX + i * dirX] === this.state.BLANK; i++);
    return i - 1;
  }


  catchCornerPiece(squares) {

    let surroundedPieces = [];
    let seen = (function () {
      let array = new Array(11).fill(null);
      for (let i = 0; i < array.length; i++) {
        let temp = new Array(11).fill(null);
        for (let j = 0; j < array.length; j++) {
          temp[j] = { "seenFlag": -1, "surroundedFlag": false };
        }
        array[i] = temp;
      }
      return array;
    })();
    // --------------------dfs-start-----------------------------
    // board[x][y]のコマについて、囲まれているか判定する.
    // 囲まれているコマの座標を返す.
    function dfs(board, seen, x, y) {

      let around = [];

      seen[y][x]["seenFlag"] = 0;
      // console.log("[y,x]?:");
      // console.log([y, x]);
      // console.dir(seen);
      // console.log("piece:?");
      // console.log(board[y][x]);
      for (let iX = -1; iX <= 1; iX++) {
        for (let iY = -1; iY <= 1; iY++) {
          // 四方のみ考える.
          if ((iX + iY === 0) || (Math.abs(iX) + Math.abs(iY) === 2)) {
            continue;
          } else {
            let nextX = x + iX;
            let nextY = y + iY;
            if (nextX >= 0 && nextX <= 10 && nextY >= 0 && nextY <= 10) {
              let nextSquare = board[nextY][nextX];
              // console.log("nextX:" + nextX, "nextY:" + nextY);
              // console.log("nextSquare:?");
              // console.log(nextSquare);

              // board[nextY][nextX]について
              // 番兵(-1)及び相手のコマの場合、aroundにtrueをpush
              //空白の場合は即座にfalseを返す.
              // コマが自身のコマだったとき再帰呼び出し

              if (nextSquare === 0) {
                // around.push(false);
                // console.log("not surrounded!!");
                // console.log(nextSquare);
                return false
                // TODO: opposeメソッドを使って相手のプレイヤーを判別すること.
              } else if (nextSquare === 1 || nextSquare === -1) {

                around.push(true);

              } else {
                // 探索済み
                if (seen[nextY][nextX]["seenFlag"] === 1) {
                  // console.log("surroundedFlag:?" + seen[nextY][nextX]["surroundedFlag"]);
                  around.push(seen[nextY][nextX]["surroundedFlag"])

                } else if (seen[nextY][nextX]["seenFlag"] === 0) {
                  continue;
                }

                else {
                  let result = dfs(board, seen, nextX, nextY);
                  // console.log("result:?");
                  // console.log(result);
                  around.push(result);

                }

              }

            }

          }

        }

      }
      // around内の要素がすべてtrueならば、trueを返す
      // console.log("arround?:")
      console.dir(around);
      seen[y][x]["seenFlag"] = 1;
      if (around.every((element) => element === true)) {
        // console.log("all true?:")
        // console.dir(around);
        surroundedPieces.push([y, x]);
        seen[y][x]["surroundedFlag"] = true;
        return true;
      } else {
        return false;
      }
    }
    // --------------------dfs-end-------------------------------

    for (let iY = 1; iY <= 9; iY++) {
      for (let iX = 1; iX <= 9; iX++) {
        if (!((iX >= 2 && iX <= 8) && (iY >= 2 && iY <= 8))) {
          // TODO: opposeで相手プレイヤーを判断すること.
          if (squares[iY][iX] === 2) {
            dfs(squares, seen, iX, iY);

          }
        }
      }
    }


    // 挟まれているコマを空白にする.
    squares = (function () {

      // console.log(surroundedPieces);

      for (let i = 0; i < surroundedPieces.length; i++) {
        let y = surroundedPieces[i][0];
        let x = surroundedPieces[i][1];
        squares[y][x] = 0;
      }
      return squares;

    })();
    return squares;

  }


  render() {
    let squares = this.state.squares;
    // if (this.state.fromSquare.length > 0 && this.state.toSquare.length > 0) {
    //   squares = this.movePiece(squares, this.state.fromSquare, this.state.toSquare)
    // }
    return (
      <div className="game">
        <div className="game-board">
          <Board squares={squares} onClick={(x, y) => this.onClickPiece(x, y)} />
        </div>
        <div className="game-info"></div>
      </div>
    )
  }
}




function App() {


  return (
    <div>
      <Game></Game>
    </div>
  );
}

export default App;
